import Circle from "./Circle";
import Line from "./Line";
import { predicateBasedOnProbability, randomFromRange } from "../utils/math";
import { State } from "../Canvas";

export interface QuadrantDimentions {
  w: number;
  h: number;
}

export default class Constellation {
  static readonly DEFAULT_ROWS: number = 5 as const;
  static readonly DEFAULT_COLUMNS: number = 7 as const;
  static readonly DEFAULT_PADDING = 20 as const;
  static readonly DEFAULT_RADIUS_MIN = 2 as const;
  static readonly DEFAULT_RADIUS_MAX = 7 as const;

  private readonly cnv: HTMLCanvasElement;
  private readonly ctx: CanvasRenderingContext2D;
  private readonly circles: Circle[];
  private readonly lines: Line[];

  private _rows = 5;
  private _columns = 7;
  private _padding: number = Constellation.DEFAULT_PADDING;
  private _radiusMin: number = Constellation.DEFAULT_RADIUS_MIN;
  private _radiusMax: number = Constellation.DEFAULT_RADIUS_MAX;
  private _maxCircles = Constellation.calculateMaxCircles(
    Constellation.DEFAULT_COLUMNS,
    Constellation.DEFAULT_ROWS
  );
  private _linesAmount = Constellation.calculateLinesAmount(
    Constellation.DEFAULT_COLUMNS,
    Constellation.DEFAULT_ROWS
  );

  public get rows() {
    return this._rows;
  }
  public get columns() {
    return this._columns;
  }
  public get padding() {
    return this._padding;
  }
  public get radiusMin() {
    return this._radiusMin;
  }
  public get radiusMax() {
    return this._radiusMax;
  }
  public get maxCircles() {
    return this._maxCircles;
  }
  public get linesAmount() {
    return this._linesAmount;
  }

  get getCircles() {
    return this.circles;
  }

  get getLines() {
    return this.lines;
  }

  private static calculateLinesAmount(columns: number, rows: number) {
    return columns - 1 + (2 * (rows - 1) + 3 * (columns - 1) * (rows - 1));
  }

  private static calculateMaxCircles(columns: number, rows: number) {
    return rows * columns;
  }

  constructor(cnv: HTMLCanvasElement, ctx: CanvasRenderingContext2D) {
    this.cnv = cnv;
    this.ctx = ctx;
    this.setValuesBasedOnViewport();

    const circles: Circle[] = Array(this._maxCircles);
    const lines: Line[] = Array(this._linesAmount);

    for (
      let i = 0;
      i < this._maxCircles + (this._linesAmount - this._maxCircles);
      i++
    ) {
      if (i < this._maxCircles) circles[i] = new Circle(ctx);
      lines[i] = new Line(ctx);
    }

    this.circles = circles;
    this.lines = lines;

    this.calculateParticleFieldPosition();
  }

  update(elapsed: number, state: State): this {
    for (
      let i = 0;
      i < this._maxCircles + (this._linesAmount - this._maxCircles);
      i++
    ) {
      if (i < this._maxCircles) this.circles[i].update(elapsed);
      this.lines[i].update(state);
    }

    return this;
  }

  render() {
    for (
      let i = 0;
      i < this._maxCircles + (this._linesAmount - this._maxCircles);
      i++
    ) {
      if (i < this._maxCircles) this.circles[i].draw();
      this.lines[i].draw();
    }
  }

  recalculate() {
    this.setValuesBasedOnViewport();

    if (this.circles.length > this._maxCircles) {
      for (
        let i = this.circles.length - this._maxCircles - 1;
        i < this.circles.length;
        i++
      ) {
        this.circles[i].hide = true;
      }
    }

    if (this.lines.length < this._linesAmount) {
      for (
        let i = this._linesAmount - this.lines.length - 1;
        i < this._linesAmount;
        i++
      ) {
        if (!this.lines[i]) {
          this.lines.push(new Line(this.ctx));
        }
      }
    }

    this.calculateParticleFieldPosition();
  }

  private setValuesBasedOnViewport() {
    if (this.cnv.width < 1000) {
      this._padding = 10;
      this._columns = 5;
      this._rows = 7;
      this._radiusMin = Constellation.DEFAULT_RADIUS_MIN;
      this._radiusMax = Constellation.DEFAULT_RADIUS_MAX;
    } else if (this.cnv.width < 2000) {
      this._padding = Constellation.DEFAULT_PADDING;
      this._columns = Constellation.DEFAULT_COLUMNS;
      this._rows = Constellation.DEFAULT_ROWS;
      this._radiusMin = Math.floor(this.cnv.width * 0.002);
      this._radiusMax = Math.floor(this.cnv.width * 0.008);
    } else {
      this._padding = Constellation.DEFAULT_PADDING;
      this._columns = Math.floor(this.cnv.width * 0.003);
      this._rows = Math.floor(this.cnv.height * 0.005);
      this._radiusMin = Math.floor(this.cnv.width * 0.001);
      this._radiusMax = Math.floor(this.cnv.width * 0.006);
    }

    this._maxCircles = Constellation.calculateMaxCircles(
      this._columns,
      this._rows
    );

    this._linesAmount = Constellation.calculateLinesAmount(
      this._columns,
      this._rows
    );
  }

  private calculateParticleFieldPosition(): this {
    const { circles, cnv } = this;

    for (const line of this.lines) {
      line.circle1 = undefined;
      line.circle2 = undefined;
    }

    const quadrantDimentions: QuadrantDimentions = {
      w: (cnv.width - this._padding) / this._columns,
      h: (cnv.height - this._padding) / this._rows,
    };

    let linesCount = 0;
    for (let [y, i] = [0, 0]; y < this._rows; y++) {
      for (let x = 0; x < this._columns; ++x && ++i) {
        const x1 = quadrantDimentions.w * x + this._padding;
        const y1 = quadrantDimentions.h * y + this._padding;
        const x2 = quadrantDimentions.w * (x + 1) - this._padding;
        const y2 = quadrantDimentions.h * (y + 1) - this._padding;

        if (!this.circles[i]) {
          this.circles.push(new Circle(this.ctx));
        }

        const circle = circles[i];

        circle.hide = false;

        circle.setBaseX(randomFromRange(x1, x2));
        circle.setBaseY(randomFromRange(y1, y2));
        circle.radius = randomFromRange(this._radiusMin, this._radiusMax);

        linesCount = this.connectCirclesBasedOnPositioning(circle, {
          x,
          y,
          i,
          currentLineIdx: linesCount,
        });
      }
    }

    return this;
  }

  private connectCirclesBasedOnPositioning(
    currentCircle: Circle,
    {
      x,
      y,
      i,
      currentLineIdx,
    }: {
      x: number;
      y: number;
      i: number;
      currentLineIdx: number;
    }
  ): number {
    const { lines, circles } = this;

    if (x === 0 && y === 0) return currentLineIdx;
    if (x > 0 && x < this._columns) {
      const lastCircle = circles[i - 1];
      lines[currentLineIdx].circle1 = lastCircle;
      lines[currentLineIdx].circle2 = currentCircle;
      currentLineIdx++;
    }

    if (y === 0) return currentLineIdx;
    const upperCircle = circles[i - this._columns];
    lines[currentLineIdx].circle1 = upperCircle;
    lines[currentLineIdx].circle2 = currentCircle;
    currentLineIdx++;

    if (x < this._columns - 1) {
      if (predicateBasedOnProbability(0.5)) return currentLineIdx;
      const upperRightCircle = circles[i - this._columns + 1];
      lines[currentLineIdx].circle1 = upperRightCircle;
      lines[currentLineIdx].circle2 = currentCircle;
      currentLineIdx++;
    }

    if (x === 0) return currentLineIdx;
    if (predicateBasedOnProbability(0.85)) return currentLineIdx;
    const upperLeftCircle = circles[i - (this._columns + 1)];
    lines[currentLineIdx].circle1 = upperLeftCircle;
    lines[currentLineIdx].circle2 = currentCircle;
    currentLineIdx++;

    return currentLineIdx;
  }
}
