import Router, { CustomHistory } from "preact-router";
import Match from "preact-router/match";

import About from "./routes/about";
import Endeavours from "./routes/endeavours";
import Expertise from "./routes/expertise";
import Home from "./routes/home";
import JobArticle from "./routes/expertise/job-article";
import CanvasBackground from "./components/CanvasBackground";
import NavBar from "./components/NavBar";
import NotFound from "./routes/not-found";
import { createHashHistory } from "history";

export default function App() {
  return (
    <main>
      <CanvasBackground />
      <div
        style={{
          width: "100vw",
          minHeight: "100%",
          position: "absolute",
          top: 0,
          left: 0,
        }}
      >
        <Match>{NavBar}</Match>
        <Router history={createHashHistory() as unknown as CustomHistory}>
          <Home path="/" />
          <About path="/about" />
          <Expertise path="/expertise" />
          <JobArticle path="/expertise/:id" />
          <Endeavours path="/endeavours" />
          <NotFound default />
        </Router>
      </div>
    </main>
  );
}
