export const jobsSortedByStartDate = Object.freeze(
  Object.entries(jobs).sort(([_, a], [__, b]) => {
    if (!a.startDate || !b.startDate) {
      return -1;
    }

    return a.startDate?.getTime() > b.startDate?.getTime() ? -1 : 1;
  })
);
