import { Link } from "preact-router";

import classes from "./NavBar.module.css";
import Logo from "src/components/Logo";
import { RouteAwareProps } from "src/declarations";

function changeClassToActive(
  linksList: HTMLUListElement | null,
  path: string | undefined
) {
  if (!linksList || !path) return;
  for (const liElement of Array.from(linksList.children)) {
    const link = liElement.children.item(0);

    if (!path.includes(link?.getAttribute("href") ?? "")) {
      link?.classList.remove(classes["active"]);

      continue;
    }

    link?.classList.add(classes["active"]);
  }
}

// XXX: Using hash routing. Seems to be the only way to handle this in GH Pages
// maybe there's a way to separate everything into separate static files?
export default function NavBar({
  path: pathFromRouter,
}: Readonly<RouteAwareProps>) {
  const pathFromWindow = window.location.href;
  const indexOfHashedPath = pathFromWindow.indexOf("/#/");

  if (
    (pathFromRouter === undefined || pathFromRouter === "/") &&
    (indexOfHashedPath < 0 || !pathFromWindow.charAt(indexOfHashedPath + 3))
  ) {
    return null;
  }

  const path =
    indexOfHashedPath !== -1
      ? window.location.href.substring(
          indexOfHashedPath,
          window.location.href.length
        )
      : pathFromRouter;

  return (
    <nav className={classes["nav-root"]}>
      <Link path="/">
        <Logo
          classes={{
            root: classes["logo"],
            "logo-s": classes["logo-s"],
            "logo-v": classes["logo-v"],
          }}
        />
      </Link>
      <ul
        ref={(instance) => changeClassToActive(instance, path)}
        className={classes["nav-items"]}
      >
        <li className={classes["nav-item"]}>
          <Link path="/about">About</Link>
        </li>
        <li className={classes["nav-item"]}>
          <Link path="/expertise">Expertise</Link>
        </li>
        <li className={classes["nav-item"]}>
          <Link path="/endeavours">Endeavours</Link>
        </li>
      </ul>
    </nav>
  );
}
